var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno'),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5"},[_vm._m(0),_c('p',{attrs:{"data-aos":"fade-right"}},[_vm._v("Los temas de logística y cadena de suministros son una cuestión de medición y evaluación constante, no es lógico trabajar sin medir y por eso se hacen varias menciones y la forma de hacerla junto con su objetivo real.")]),_c('separador'),_vm._m(1),_vm._m(2),_c('separador'),_vm._m(3),_vm._m(4),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("El valor agregado es todo lo que el cliente intuye de la organización y en algunos casos se puede conocer como propuesta de valor que está definida por la calidad, el tiempo o cualquiera de los diferentes elementos que se muestran en esta gráfica:")]),_vm._m(5),_c('p',{attrs:{"data-aos":"fade-right"}},[_vm._v("Como toda relación, el valor no es resultado de una construcción unilateral, sino que se genera a partir de la interacción de sus participantes. En la medida que los clientes superan sus expectativas frente a la disponibilidad y el suministro de su proveedor, no solo se aferran a la marca y a la empresa repitiendo la adquisición de sus productos y servicios, sino que se convierten en referente para otros grupos de influencia y construyen la base fidelizada de clientes que proveen las utilidades a largo plazo de la empresa, los cuales, a su vez, traducen la relación de valor en la satisfacción con las cualidades –racionales y aspiracionales– del servicio obtenido, frente a los costos incurridos que en buena parte se representan en el precio de adquisición de los productos. Lo anterior es lo que se denomina valor agregado percibido por el cliente.")]),_c('p',{attrs:{"data-aos":"fade-right"}},[_vm._v("Los usuarios, consumidores y compradores finales son los últimos y principales destinatarios de esa superación de expectativas (formadas a partir de las experiencias de compra, de sus necesidades en comparación con la oferta de la competencia, o de los comentarios y opiniones externas), pero desde luego la ecuación del valor se propaga a través de los canales de toda la cadena de suministro, para cada momento decisivo en la relación cliente-proveedor.")]),_c('p',{attrs:{"data-aos":"fade-right"}},[_vm._v("La globalización de los negocios y la necesidad de llegar con más rapidez y eficiencia a los consumidores son situaciones que conducen a las empresas a buscar diversas estrategias para responder a las complejidades cada vez mayores de los mercados.")]),_vm._m(6),_c('separador'),_vm._m(7),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("En este contexto se debe ser enfático como lo hace Mora (2008, s.p.) “una de las características de las organizaciones modernas es que han incorporado a sus procesos, elementos de gestión que les permiten evaluar sus logros o señalar falencias para aplicar los correctivos necesarios”. Estos elementos conocidos como indicadores se deben establecer desde el mismo momento en que se elabora el plan de desarrollo estratégico y se aplican a este y al plan operativo, en espacios reales y en evaluación constante que busca un mejoramiento clave.")]),_vm._m(8),_vm._m(9),_c('hr',{staticClass:"borde-cab"}),_c('p',{attrs:{"data-aos":"fade-right"}},[_vm._v("El término “indicador”, en el lenguaje común, se refiere a datos esencialmente cuantitativos, que permiten dar cuenta de cómo se encuentran las cosas en relación con algún aspecto de la realidad que nos interesa conocer. Los Indicadores pueden ser: medidas, números, hechos, opiniones o percepciones que señalen condiciones o situaciones específicas. Estos indicadores deberán reflejarse adecuadamente en la naturaleza, peculiaridades y nexos de los procesos que se originan en la actividad económico-productiva, sus resultados, gastos, entre otros, y caracterizarse por ser estables y comprensibles, por tanto, no es suficiente con uno solo de ellos para medir la gestión de la empresa sino que se impone la necesidad de considerar los sistemas de indicadores, es decir, un conjunto interrelacionado de ellos que abarque la mayor cantidad posible de magnitudes a medir.")]),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("Características de los indicadores:")]),_vm._m(10),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("Aparte de sus características fundamentales, se tienen tres aspectos adicionales a tener en cuenta: sus indicadores, saber para qué se requieren y de qué sirve cada uno.")]),_c('AcordionA',{staticClass:"mb-5",attrs:{"tipo":"b","clase-tarjeta":"tarjeta fondo-purpura-medio","data-aos":"zoom-in"}},[_c('div',{staticClass:"row",attrs:{"titulo":"Indicadores logísticos"}},[_c('p',[_vm._v("Son relaciones de datos numéricos y cuantitativos aplicados a la gestión logística que permiten evaluar el desempeño y el resultado en cada proceso e incluyen los procesos de recepción, almacenamiento, inventarios, despachos, distribución, entregas, facturación y los flujos de información entre los socios de negocios. Es indispensable, entonces, que toda empresa desarrolle habilidades alrededor del manejo de los indicadores de gestión logística, con el fin de poder utilizar la información resultante de manera oportuna (tomar decisiones).")])]),_c('div',{attrs:{"titulo":"Objetivos"}},[_c('ul',{staticClass:"lista-ul--color lista-ul--separador"},[_c('li',{staticStyle:{"margin-bottom":"2px"}},[_c('i',{staticClass:"fas fa-check-circle",staticStyle:{"color":"#FE7062"}}),_c('p',{staticClass:"mb-0"},[_vm._v("Identificar y tomar acciones sobre los problemas operativos.")])]),_c('li',{staticStyle:{"margin-bottom":"2px"}},[_c('i',{staticClass:"fas fa-check-circle",staticStyle:{"color":"#FE7062"}}),_c('p',{staticClass:"mb-0"},[_vm._v("Medir el grado de competitividad de la empresa frente a sus competidores nacionales e internacionales.")])]),_c('li',{staticStyle:{"mar